import {mapState, mapGetters} from 'vuex'
export default {
  data(){
    return {
      registExpoWorkshop: {},
      // audiences: "學齡前幼兒／國小生／國中生／高中生／自學生／大專以上學生／職場新鮮人／青壯年／家長／教育工作者／投資人／創業者／學校單位／公部門／其他".split("／"),
      active: 0,
      audience_normal: true,
      success: false,
      types: [ "藝術體驗創作",
              "傳統工藝教學",
              "互動式教學",
              "科學動手實驗",
              "創新教案分享與教學",
              "小型分享座談",
              "戶外園區導覽",
              "其他"],
      timespans: [
        "10/5 (五) 13:00-14:30",
        "10/5 (五) 15:00-16:30",
        "10/5 (五) 17:00-18:30",
        "10/6 (六) 10:00-11:30",
        "10/6 (六) 13:00-14:30",
        "10/6 (六) 15:00-16:30",
        "10/6 (六) 17:00-18:30",
        "10/7 (日) 10:00-11:30",
        "10/7 (日) 13:00-14:30",
        "10/7 (日) 15:00-16:30",
        "10/7 (日) 17:00-18:30",
      ]
    }
  },
  computed: {
    ...mapState({
      auth: 'auth',
      user: state=>state.auth.user,

      token: state=>state.auth.token
    }),
  },
  mounted(){
    console.log(this.user)
    this.userClone = JSON.parse(JSON.stringify(this.user))
  },
  methods: {
    updateUserInfo(){
       this.axios.post(
          `/api/auth/user/update/info`,
          { 
            token: this.token,
            user: this.userClone
          }
        ).then(res=>{
          this.$message({
            message: '資料更新成功',
            type: 'success'
          });
          // let _this  = this
          console.log(res.data.user)
          // setTimeout(function(){
          // this.$router.push(`/manage/${this.$route.meta.type}/`+res.data.data.id)
          // },300)
        })
    },
    prev() {
      if (this.active-- < 0) this.active = 2;
      window.scrollTo(0,0)
    }, 
    next() {
      if (this.active++ > 2) this.active = 0;
      window.scrollTo(0,0)
    }
  }
}



